<template>
  <div class="boxwrapper">
    <input type="text" placeholder="Enter Existing Donor" v-model="query" @keyup.up="matchup" @keyup.down="matchdown">
    <ul class="autocomplete" v-if="query!=''">
     <li class="match" v-for="match in matches" :id="'match-'+match.id" @click="selectmatch(match.id)">
     {{match.Firstname}} {{match.Lastname}} 
     <span v-if="match.Phone!=''">- {{match.Phone}}</span> 
     </li>
     </ul>
   </div>
</template>

<script>

//https://alligator.io/vuejs/add-v-model-support/
//@todo make so up/down change highlight

export default {
  name: 'Autocomplete',
  props: {
  },
  methods:{
    selectmatch:function(selid){
      this.$emit('selected',this.donors.filter((donor) => donor['id']==selid)[0]);
      this.query=''
    },
    changematch:function(dir){
      var nextsel
      if(this.highsel == null){
        nextsel = 0
      }
      else{
        nextsel = this.highsel+dir
      }
      this.highsel=nextsel
      this.matches[nextsel]['id']
      alert(this.highsel)
    },
  
    matchup:function(){
      this.changematch(+1)
    },
    matchdown:function(){
      this.changematch(-1)
    },
  },
  computed:{
    donors:{
      get(){
        return this.$store.state.donors
      },
      set(){

      }
    },
    matches(){
      if (this.query == ''){
        return [];
      }
      else{
        var filtered = this.donors.filter((donor) => 
            donor['Firstname'].toLowerCase().includes(this.query.toLowerCase()) || 
            donor['Lastname'].toLowerCase().includes(this.query.toLowerCase()) ||
            donor['Phone'].includes(this.query)

          );
        
        return filtered;
      }
    }
  },
  data(){
    return{
      query:'',
      highsel:null,
      numspots:10,
    }
  }

}
</script>

