<template>
  <div class="modulewrapper">
  <button @click='togglemodal' class="adddon">Add New Donor</button>
  <Autocomplete @selected="setsel" class="auto"/>
  <p>
    <div v-if="!isblank" class="seldon">
      <button class="seldel" @click="clearsel">x</button>
        {{selecteddonor.Firstname}}, {{selecteddonor.Lastname}} donated:
    </div>
  <add-new-donor-modal v-if="addingnew" @sel='confsel' @close='togglemodal'/>
  </div>
</template>

<script>
//@todo if there's text in the autocompleter put that as default for modal
//@todo check if donor already exists in db
import Autocomplete from './Autocomplete'
import addNewDonorModal from './addNewDonorModal'
export default {
  name: 'donorModule',
  components:{Autocomplete,addNewDonorModal},
  props: {
    donors:Array,
    msg: String,
    category:String
  },
  methods:{
    clearsel:function(){
        this.selecteddonor=null
    },
    confsel:function(sel){
      this.setsel(sel)
      this.togglemodal()
    },
    setsel: function(sel){
      this.selecteddonor=sel
    },
    togglemodal:function(){
      this.addingnew=!this.addingnew;
    }
  },
  computed:{
    isblank(){
      return this.selecteddonor==null
    },
    selecteddonor:{
        get(){
          return this.$store.state.seldonor
        },
        set(value){
          this.$store.dispatch('selDonor',value)
        },
  },
},
  data(){
    return{
      test:5,
      addingnew:false,
      }
    }
  }

</script>

