// store.js 

import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'

Vue.use(Vuex) // only required if you're using modules.
              // We're using modules, so there you go.
Vue.use(VueAxios, axios)
//Vue.http.headers.common['Access-Control-Allow-Origin'] = 'http://localhost:8000'
const endpoint = process.env.VUE_APP_API_ENDPOINT
const dataep = endpoint+'dock/api/'
const donorep = dataep+'donors/'
const store = new Vuex.Store({
//
  state: {
    seldump:0,
    seldons:{},
    seldonor:null,
    donors:[],
    data: {
      "donors":[],
      "donations":[],
      "donationlines":[],
      "donationcategories":[],
      "categorymaps":[]
    }
  },
//
  mutations: {
    'ADD_DONOR'(state,emp){
      state.employees.push(emp)
    },
    'SET_DONORS'(state,donrs){
      donrs.map((don) => {
        state.donors.push(don);
        console.log(don)})
    },
   'SET_SELDONS'(state,dons){
      state.seldons=dons
   },
   'SET_SELDUMP'(state,dump){
      state.seldump=dump
   },
   'SET_SELDONOR'(state,donor){
      state.seldonor=donor
   },

    'ADD_DONATION'(state,don,lines){
      state.data.donations.push(don)
      state.data.donationlines.push(don)
    },
  },
//
  actions: {
  loadDataFrom({commit},format="REST")  {
    if(format=="REST"){
      axios.get(donorep)
        .then((response)=> {
           commit('SET_DONORS',response.data)
                      }, (err) => {
        console.log('couldnt set donors (from '+donorep+'):: '+err)
      })
      }
    },

  postData({commit},data,format="REST"){
    if(format=="REST"){
      //@todo include authentication
      //axios.post(endpoint,{""}) 
    }
  },
  selDons({commit},don){
    commit('SET_SELDONS',don)
  },
  selDump({commit},dump){
    commit('SET_SELDUMP',dump)
  },
  selDonor({commit},donor){
    commit('SET_SELDONOR',donor)
  },
}})


export default store